<template>
	<div class="participated-form">
		<main-bar title="我为别人填写的表单" :operations="false"></main-bar>
		<el-table
			class="form-content"
			:data="formData"
			style="width: 100%; font-size: 18px"
			max-height="100%">
			<el-table-column
				show-overflow-tooltip
				label="名称"
				width="500">
				<template slot-scope="scope">
					<icon class="li__form-icon" icon="gd-form"></icon>
					<span>{{scope.row.formTitle}}</span>
				</template>
			</el-table-column>
			<el-table-column
				prop="formDataCountByUser"
				label="我提交的数量">
			</el-table-column>
			<el-table-column
				prop="formDataUpdateTime"
				label="数据最后提交时间">
			</el-table-column>
			<el-table-column label="操作" width="150">
				<template slot-scope="scope">
					<span class="pf-operation" @click="fillAgain(scope.row.formUrl)">再填写一次</span>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>

<script>
import MainBar from '@/components/content/formDisplayType/MainBar.vue'
import {getFillesForms} from '@/network/form.js'
	export default {
		name: 'ParticipatedForms',
		data() {
			return {
				formData: []
			}
		},
		components: {
			MainBar
		},
		methods: {
			getFillesFormsData() {
				getFillesForms().then(res => {
					console.log("填写：", res.data);
					this.formData = res.data;
				})
			},
			fillAgain(url) {
				this.$router.push(url)
			}
		},
		created() {
			this.getFillesFormsData()
		}
	}
</script>

<style>
	.el-table--enable-row-hover .el-table__body tr:hover > td {
		font-size:1.2em !important;
	}
	.form-content {
		margin-top: 20px;
	}
	.pf-operation {
		color: var(--color-text-change);
	}
	.pf-operation:hover {
		color: var(--color-btn-focus);
	}
	.pf-operation:active {
		color: var(--color-btn-pressed);
	}
</style>
